<template>
    <div>
        <el-row>
            <headers :title='title'></headers>
        </el-row>
        <el-row>
            <el-col :span='18' :offset='3'>
                <div class="data_list">
                    <div class="data_l_t">
                        <span>数据列表</span>
                        <el-button type="primary" @click='ad' icon='el-icon-plus'>添加</el-button>
                        <el-dialog title="新增岗位类型" :visible.sync="dialogVisible" width="30%">
                            <el-form label-width='100px' ref="form" :model="form" :rules="rules">
                                <el-form-item label='岗位分类:' prop='typename'>
                                    <el-input id='inputs' placeholder='输入岗位类型' v-model='form.typename' maxlength='20' clearable></el-input>
                                </el-form-item>
                            </el-form>
                            <span slot="footer" class="dialog-footer">
                                <el-button @click="dialogVisible = false">取 消</el-button>
                                <el-button type="primary" @click="add('form')">确 定</el-button>
                            </span>
                        </el-dialog>
                    </div>
                    <div>
                        <el-table :data="post_list.slice((currentPage-1)*pagesize,currentPage*pagesize)" style="width: 100%" :header-cell-style="rowClass" :cell-style="cellStyle" border @row-click='haedle'>
                            <el-table-column prop="id" label="序号" width="100">
                            </el-table-column>
                            <el-table-column prop="typeName" label="岗位类型">
                            </el-table-column>
                            <el-table-column prop="realName" label="创建人" width="100">
                            </el-table-column>
                            <el-table-column prop="creationTime" label="创建时间" width="200">
                            </el-table-column>
                            <el-table-column label="操作" width="150">
                                <template scope='scope'>
                                    <span style='cursor:pointer;color:#0079FE;' @click.stop='del(scope.$index,scope.row.id)'><i class="el-icon-delete"></i> 删除</span>
                                </template>
                            </el-table-column>
                        </el-table>
                        <el-row>
                            <div class="pages">
                                <el-pagination background @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page.sync="currentPage" :page-size="pagesize" layout="prev, pager, next, jumper" :total="total">
                                </el-pagination>
                            </div>
                        </el-row>
                        <el-dialog title="修改岗位类型" :visible.sync="dialogVisibles" width="30%">
                            <el-form label-width='100px' ref="form" :model="form" :rules="rules">
                                <el-form-item label='岗位分类:' prop='typename'>
                                    <el-input id='inputs' placeholder='输入岗位类型' v-model='form.typename' maxlength='20' clearable></el-input>
                                </el-form-item>
                            </el-form>
                            <span slot="footer" class="dialog-footer">
                                <el-button @click="dialogVisibles = false">取 消</el-button>
                                <el-button type="primary" @click="add2('form')">确 定</el-button>
                            </span>
                        </el-dialog>
                    </div>
                </div>
            </el-col>
        </el-row>
    </div>
</template>
<script>
import headers from '../../components/header.vue'
export default {
    components: {
        headers
    },
    data() {
        return {
            title: '岗位类型',
            currentPage: 1,
            pagesize: 20,
            total: 1,
            post_list: [],
            dialogVisible: false,
            dialogVisibles: false,
            typename: '',
            up_id: '',
            form: {
                typename: ''
            },
            rules: {
                typename: [{
                    required: true,
                    message: '请输入岗位类型',
                    trigger: 'blur'
                }, {
                    min: 1,
                    max: 20,
                    message: '最大长度为20'
                }]
            }
        }
    },
    methods: {
        /*打开添加弹框*/
        ad() {
            this.dialogVisible = true
            setTimeout(() => {
                let inputelement = document.getElementById('inputs')
                inputelement.focus()
                this.form.typename=''
            }, 1)
        },
        add(formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    var id = window.sessionStorage.getItem('userId')
                    this.dialogVisible = false
                    this.$axios.post('/tomlive/talentType/insertSelectTalentType', this.$qs.stringify({
                        typeName: this.form.typename,
                        userId: id
                    })).then((res) => {
                        console.log(res)
                        this.all()
                        this.form.typename = ''
                    })
                } else {
                    console.log('error submit!!');
                    return false;
                }
            })
            /*var id = window.sessionStorage.getItem('userId')
            this.dialogVisible = false
            this.$axios.post('/tomlive/talentType/insertSelectTalentType', this.$qs.stringify({
                typeName: this.form.typename,
                userId: id
            })).then((res) => {
                console.log(res)
                this.all()
                this.form.typename = ''
            })*/
        },
        del(index, id) {
            this.$confirm('是否删除?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then((action) => {
                if (action === 'confirm') {
                    this.$axios.post('/tomlive/talentType/deleteTalentTypeByPrimaryKey', this.$qs.stringify({
                        id: id
                    })).then((res) => {
                        console.log(res)
                        this.all()
                    })
                }
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消'
                });
            })
        },
        /*修改岗位类型*/
        haedle(row, event, column) {
            this.dialogVisibles = true;
            this.form.typename = row.typeName
            this.up_id = row.id
        },
        add2(formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    this.dialogVisibles = false
                    this.$axios.post('/tomlive/talentType/updateByPrimaryKeySelective', this.$qs.stringify({
                        typeName: this.form.typename,
                        id: this.up_id
                    })).then((res) => {
                        console.log(res)
                        this.all()
                        this.form.typename = ''
                    })
                } else {
                    console.log('error submit!!');
                    return false;
                }
            })
            /*var id = window.sessionStorage.getItem('userId')
            this.dialogVisible = false
            this.$axios.post('/tomlive/talentType/insertSelectTalentType', this.$qs.stringify({
                typeName: this.form.typename,
                userId: id
            })).then((res) => {
                console.log(res)
                this.all()
                this.form.typename = ''
            })*/
        },
        rowClass({ row, rowIndex }) {
            return "text-align:center;background:#f5f5f5;color:#666"
        },
        cellStyle({ row, column, rowIndex, columnIndex }) {
            return "text-align:center;"
        },
        handleSizeChange(val) {
            // console.log(`每页 ${val} 条`);
        },
        handleCurrentChange(val) {
            // console.log(`当前页: ${val}`);
        },
        /*查询全部岗位类型*/
        all() {
            this.$axios.post('/tomlive/talentType/selectAllTalentType').then((res) => {
                console.log(res)
                this.post_list = res.data.info.reverse()
                this.total = this.post_list.length
                console.log(this.post_list)
            })
        }
    },
    mounted() {
        this.all()
    }
}
</script>
<style scoped>
.data_list {
    width: 100%;
    min-height: 800px;
    border: 1px solid #e9e9e9;
    box-sizing: border-box;
    background: #fff;
    margin: 20px 0;
    padding: 0px 20px;
}

.data_l_t {
    width: 100%;
    height: 60px;
    font-size: 14px;
    line-height: 60px;
    color: #666;
    font-weight: bold;
}

.data_l_t>button {
    float: right;
    margin: 10px 0;
}

.pages {
    margin: 20px 0 20px 0;
    float: right;
}
</style>
<style>
.el-dialog__header {
    width: 100%;
    height: 50px;
    background: #f5f5f5;
    padding: 0;
}

.el-dialog__header>span {
    margin-left: 20px;
    font-size: 16px;
}
.el-dialog__title{
	line-height: 50px;
}
</style>